<template>
 <div>
  <h1>Blog</h1>
       <b-container>
              <blog-card 
                    v-for="fix in fixs" 
                    :key="fix.id"
                    :name="fix.name"
                    :autor="fix.autor"
                    :tema="fix.tema"
                    :link="fix.link"></blog-card>
       </b-container>
</div>
</template>

<script>
import blogcard from '@/components/blogcard.vue';

export default {
  name: "blog",
  components: {"blog-card":blogcard},
  mounted(){
    this.fetchData();
  },
  data(){
    return{
      fixs: []
    }
  },
  methods:{
    async fetchData(){
      const res = await fetch ("fixs.json");
      const val = await res.json();
      this.fixs = val;
      console.log (val);
    }
  }
};
</script>

