<template>
    <div>
        <h1>Fromulari</h1>
        <form>
            <b-container>
            <b-row class="aling-content-md-center mt-4">
                <b-col col md="12">
                    <b-card
                    header="Registra't i tindràs més descomptes"
                    border-variant="warning"
                    header-bg-variant="warning"
                    header-text-variant="black"
                    align="center">
      <b-card-text>
      <b-form @submit="onSubmit">
      <b-form-group 
       label="Usuari"
       label-align-sm="left"
       description="L'usuari ha de tenir més de 4 caràcters"
        >
        <b-form-input v-model="usuari" :state="validarusuari" required></b-form-input>
     </b-form-group>

      <b-form-group 
        label="Contrasenya"
        label-align-sm="left"
        description="La contrassenya ha de tenir més de 5 caràcters."
        >
         <b-form-input v-model="contrasenya" :state="validarcontrasenya" type="password" required></b-form-input>
      </b-form-group>

      <b-form-group 
        label="Correu electrònic"
        label-align-sm="left"
        description="No compartirem amb ningú més el teu correu electronic."

        >
         <b-form-input v-model="correu" ></b-form-input>
      </b-form-group>

     <b-form-group                                    
        label-cols-lg="3"
        label="Adreça d'enviament"
        label-size="lg"
        label-class="font-weight-bold pt-0"
        class="mb-0"
    >
      <b-form-group
        label="Carrer"
        label-for="nested-carrer"
        label-cols-sm="3"
        label-align-sm="right"
      >
        <b-form-input v-model="carrer" id="nested-carrer"></b-form-input>
      </b-form-group>

      <b-form-group
        label="Ciutat:"
        label-for="nested-ciutat"
        label-cols-sm="3"
        label-align-sm="right"
      >
        <b-form-input v-model="ciutat" id="nested-ciutat"></b-form-input>
      </b-form-group>

      <b-form-group
        label="Codi Postal:"
        label-for="nested-codiposat"
        label-cols-sm="3"
        label-align-sm="right"
      >
        <b-form-input v-model="codipostal" :state="validarcodipostal" type="number" id="nested-codipostal"></b-form-input>
      </b-form-group>

      <b-form-group
        label="País:"
        label-for="nested-ca"
        label-cols-sm="3"
        label-align-sm="right"
      >
        <b-form-input v-model="ca" id="nested-ca"></b-form-input>
      </b-form-group>

      <b-form-group
        label="Pagament amb:"
        label-cols-sm="3"
        label-align-sm="right"
        class="pt-0"
        v-slot="{ ariaDescribedby }"
      >
        <b-form-radio-group
          class="pt-2"
          :options="['PayPal', 'Karma', 'Tarjeta de crèdit']"
          :aria-describedby="ariaDescribedby"
        ></b-form-radio-group>
      </b-form-group>
    </b-form-group>
                              <b-form-group>
                                 <b-button type="submit" variant="outline-warning" size="lg" :disabled="acceptableSubmittion">Registar-me</b-button>
                              </b-form-group>     
                          </b-form> 
                      </b-card-text>    
                    </b-card>

                    
                </b-col>
            </b-row>
            </b-container>
        </form>
    </div>
</template>

<script>
export default {
    name: 'usuari',
    data() {
        return {
            usuari: '',
            contrasenya: '',
            carrer: '',
            ciutat: '',
            codipostal: '',
            ca: '', 
            correu: ''
        }
    },
    methods:{
        onSubmit(event){
            event.preventDefault()
            alert(`Vosté ha guardat ${this.usuari} com el vostre usuari i ha posat com a contrasenya ${this.contrasenya}  dins el nostre sistema de dades`)
        }
    },
    computed: {
        acceptableSubmittion(){
          return this.usuari.length > 0 && this.contrasenya.length > 0 ? false : true  
    },
         validarusuari(){
          return this.usuari.length > 4 ? true : false      
    },
         validarcontrasenya(){
          return this.contrasenya.length > 5 ? true : false      
    },
        validarcodipostal(){
          return this.codipostal.length == 5 ? true : false      
    }

}
}
 
</script>

<style>
.row{
  justify-content: center;
}

.h4{
  font-size: 1rem;
}

.h1, h1  {
    font-size: 2.7rem;
    padding-top: 2rem;
    padding-bottom: 1.2rem;
    }
p{
 margin: 1rem;
 font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
 font-size: 16px;
}
</style>
